html,
body {
  background-color: #f1f0f3;
  margin: 10px;
}

.navbar {
  background-color: #4b405f;
  margin-bottom: 1em;
}

.header {
  background-color: #4b405f;
  margin-bottom: 1em;
}

footer {
  background-color: #59359c;
  color: #4b405f;
}

.container {
  display: grid;
  grid-gap: 3px;

  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(10, 1fr);
  max-height: 100vh;
}

.navbar {
  display: grid;
  grid-template-columns: repeat(6, 1fr); 
  justify-items: center;

  grid-column: 5/-1;
  grid-row: 1/2;
}

.header {
  grid-column: 1/5;
  grid-row: 1/2;
}

footer {
  grid-column: 1/-1;
  grid-row: 10/11;
}





